body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}


ul {
    padding: 0;
    padding-left: 30px;
    list-style: none;
    background: #f2f2f2;
}
ul li {
    display: inline-block;
}
ul li a {
    display: block;
    padding: 10px 25px;
    color: #333;
    text-decoration: solid;
}
ul li a:hover {
    color: #fff;
    background: #939393;
}

.mainTitle {
    font: 50px "Calibre", Futura, san-serif;
    margin: 30px;
    block-size: auto;
}

.subTitle {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: xx-large;
    margin: 30px;
}

.titleCentered {
    position: fixed;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
}